import React from 'react';
import style from './CallbackModal.module.scss';
import formStyle from '../../../../styles/common/Form.module.scss';
import { useFormik } from 'formik';
import { useDispatch } from 'react-redux';
import { AppDispatch } from '../../../../redux/store';
import { sendCallbackRequestTC } from '../../../../redux/reducers/basket-reducer';

const CallbackModal = (  ) => {

  const dispatch = useDispatch<AppDispatch>();

  const formik = useFormik( {
    initialValues: {
      name: '',
      phoneNumber: '',
    },
    validate: (values) => {
      const errors: FormikCallbackErrorType = {};
      if (values.name.length < 2){
        errors.name = '   ';
      }
      if(!values.phoneNumber){
        errors.phoneNumber = '   '
      } else if ( values.phoneNumber.length !==13 ){
        errors.phoneNumber = '  13 '
      } else if(!/^[+]{1}375(29|25|33|44)[0-9]{7}$/i.test(values.phoneNumber)){
        errors.phoneNumber = ', ,    +375291234567'
      }
      return errors;
    },
    onSubmit: value => {
      formik.resetForm();
      dispatch(sendCallbackRequestTC({name: value.name, phoneNumber: value.phoneNumber}));
    },
  } );

  return (
        <form className={ style.formBlock } onSubmit={ formik.handleSubmit }>
          <h3>    15 </h3>
          <div className={ formStyle.formInfo }>
            <div className={ formStyle.formInput }>
              <p></p>
              <input
                type={ 'name' }
                placeholder={ '  ' }
                { ...formik.getFieldProps( 'name' ) }
              />
              {formik.touched.name && formik.errors.name &&
                <span>{formik.errors.name}</span>
              }
            </div>

            <div className={ formStyle.formInput }>
              <p> </p>
              <input
                type={ 'phoneNumber' }
                placeholder={ '+375291231212' }
                { ...formik.getFieldProps( 'phoneNumber' ) }
              />
              {formik.touched.phoneNumber && formik.errors.phoneNumber &&
                <span>{formik.errors.phoneNumber}</span>
              }
            </div>

          </div>
          <div className={ formStyle.orderBlock }>
            <button type="submit"></button>
            <p>       
            <span onClick={ () => alert( '  pdf ' ) }>  </span></p>
          </div>
        </form>
  );
};

export default CallbackModal;

type FormikCallbackErrorType = {
  name?: string,
  phoneNumber?: string
}
